  font-family: "Pacifica"; 
  src: url("//db.onlinewebfonts.com/t/3ade034999153f8110b40724ad74f00e.eot"); 
  src: url("//db.onlinewebfonts.com/t/3ade034999153f8110b40724ad74f00e.eot?#iefix") format("embedded-opentype"), 
       url("//db.onlinewebfonts.com/t/3ade034999153f8110b40724ad74f00e.woff2") format("woff2"), 
       url("//db.onlinewebfonts.com/t/3ade034999153f8110b40724ad74f00e.woff") format("woff"), 
       url("//db.onlinewebfonts.com/t/3ade034999153f8110b40724ad74f00e.ttf") format("truetype"), 
       url("//db.onlinewebfonts.com/t/3ade034999153f8110b40724ad74f00e.svg#Pacifica") format("svg"); 
}
@font-face {
  font-family: "Avenir Light"; 
  src: url("//db.onlinewebfonts.com/t/116af611cbcd9e4bada60b4e700430c1.eot"); 
  src: url("//db.onlinewebfonts.com/t/116af611cbcd9e4bada60b4e700430c1.eot?#iefix") format("embedded-opentype"), 
       url("//db.onlinewebfonts.com/t/116af611cbcd9e4bada60b4e700430c1.woff2") format("woff2"), 
       url("//db.onlinewebfonts.com/t/116af611cbcd9e4bada60b4e700430c1.woff") format("woff"), 
       url("//db.onlinewebfonts.com/t/116af611cbcd9e4bada60b4e700430c1.ttf") format("truetype"), 
       url("//db.onlinewebfonts.com/t/116af611cbcd9e4bada60b4e700430c1.svg#Avenir Light") format("svg"); 
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;


  --navbar-height: 80px;
  --navbar-button-width: 150px;
  --navbar-button-height: 45px;

  --content-options-button-width: 300px;
  --content-options-button-height: 90px;
  background: url(./assets/red_vs_blue_edited.jpg) no-repeat fixed center;
  background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a{
  text-decoration: none;
}

a>hr{
  color: white;
}

.navbar{
  height: var(--navbar-height);
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.nav-group{
  display: flex;
  width: 50%;
}

.nav-item{
  margin: 20px;
  width: var(--navbar-button-width);
  height: var(--navbar-button-height);
}

.nav-item-shadow{
  z-index: -1;
  position: fixed;
  width: var(--navbar-button-width);
  height: var(--navbar-button-height);
  border-radius: 5px;
  box-shadow: 10px 10px 0 rgb(73, 33, 62);
  
}

.nav-button{
  display: flex;
  justify-content: center;
  align-items: center;

  width: var(--navbar-button-width);
  height: var(--navbar-button-height);

  background-color: rgb(14, 10, 72, 0.31);

  color: white;
  font-size: 26px;
  font-family: Avenir Light;
  border-radius: 5px;

  transition: color 0.1s ease-out, transform 0.1s ease-out, background-color 0.1s ease-out;
}

.nav-item:hover > .nav-button{
  transform: translate(10px, 10px);
  color: black;
  background-color: rgb(255, 255, 255, 1);
}

.content{
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  align-items: center;
}

.home{
  width: 100%;
  height: calc(100vh - var(--navbar-height));
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  align-items: center;
  transition: height 500ms;
}


.title{
  flex: 0 1 auto;
}

.title>h1{
  padding: 0;
  margin: 0;
  font-size: 152px;
  font-family: Pacifica;
  font-weight: bold;
  color: white;
  text-shadow: 3px 3px rgb(67, 124, 178, 0.8), -3px -3px rgb(157, 71, 110, 0.8);
}

.title>hr{
  padding: 0;
  margin: 0;
}

.title>p{
  font-family: Avenir Light;
  font-size: 18px;
  color: white;
  letter-spacing: 10px;
}

.content-options{
  flex: 0.75 1 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.content-options-button{
  margin: 20px;
  width: var(--content-options-button-width);
  height: var(--content-options-button-height);
}

.content-options-button-shadow{
  z-index: -1;
  position: fixed;
  width: var(--content-options-button-width);
  height: var(--content-options-button-height);
  border-radius: 5px;
  box-shadow: 20px 20px 0 rgb(7, 16, 100);
  
}

.content-options-button-text{
  display: flex;
  justify-content: center;
  align-items: center;

  width: var(--content-options-button-width);
  height: var(--content-options-button-height);

  background-color: rgb(33, 27, 114, 0.85);

  color: white;
  font-size: 52px;
  font-family: Avenir Light;
  border-radius: 5px;

  transition: color 0.1s ease-out, transform 0.1s ease-out, background-color 0.1s ease-out;
}


.content-options-button:hover > .content-options-button-text{
  transform: translate(20px, 20px);
  color: black;
  background-color: rgb(255, 255, 255, 1);
}

.FAQ{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 90%;
  height: 65%;
  border-radius: 10px;
  background-color: black;
  color: white;
}
